<template>
  <div v-if="post" class="post">
    <div>
      <Panel>
        <template #content2>
          <div class="post-header">
            <h2>
              <el-tag v-if="post.top" size="mini" type="success" style="margin-right:10px">置顶</el-tag>
              <el-tag v-else-if="post.good" size="mini" type="success" style="margin-right:10px">精华</el-tag>
              {{post.title}}
            </h2>
            <div class="post-info">
              <ul class="tips">
                <li>发布于{{post.last_reply_at|moment('from','now')}}</li>
                <li>作者{{post.author.loginname}}</li>
                <li>{{post.visit_count}}次浏览</li>
                <li>来自{{post.tab|transformTab}}</li>
              </ul>
              <el-button
                @click="collect"
                size="mini"
                :type="post.is_collect?'info':'success'"
                v-if="info"
              >{{post.is_collect?'取消收藏':'收藏'}}</el-button>
            </div>
            <el-button size="mini" @click="edit">编辑</el-button>
          </div>
          <div class="post-content" v-html="post.content"></div>
        </template>
      </Panel>

      <Panel v-if="info">
        <template #header>添加回复</template>
        <template #content>
          <!-- md编辑器 -->
          <mavon-editor v-model="content" defaultOpen="edit" :subfield="false" :boxShadow="false" />
          <!-- <textarea v-model="content" name="" id="" cols="30" rows="10"></textarea> -->
          <el-button type="primary" @click="add">回复</el-button>
        </template>
      </Panel>
      <Panel>
        <template #header>
          <span>{{post.reply_count}}回复</span>
        </template>
        <template #content>
          <PostCommentItem
            v-for="(comment,index) in post.replies"
            :key="index"
            v-bind="{index,...comment}"
          />
        </template>
      </Panel>
    </div>
    <AsideInfo :loginname="post.author.loginname" />
  </div>
  <el-skeleton v-else :rows="6" animated></el-skeleton>
</template>

<script>
import Panel from "../containers/Panel.vue";
import PostCommentItem from "../components/PostCommentItem.vue";
import AsideInfo from '../components/AsideInfo.vue'
import { mapActions, mapState } from "vuex";
export default {
  components: {
    Panel,
    PostCommentItem,
    AsideInfo
  },
  data() {
    return {
      content: ""
    };
  },
  async created() {
    await this.getPost(this.id);
    console.log(this.post);
  },
  computed: {
    id() {
      return this.$route.params.id;
    },
    ...mapState({
      post: state => state.post.post,
      info: state => state.login.info,
    })
  },
  methods: {
    ...mapActions(["getPost", "collectHandle", "addComment"]),
    // 收藏
    collect() {
      console.log(this.post.is_collect);
      this.collectHandle({
        id: this.post.id,
        is_collect: !this.post.is_collect
      });
    },
    // 添加评论
    add() {
      if (this.content.trim()) {
        this.addComment({ id: this.post.id, content: this.content });
      }
    },
    // 编辑
    edit(){
      this.$router.push(`/post/${this.id}/edit`)
    }
  }
};
</script>

<style lang="less">
.post{
  display: flex;
  .post-header {
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
    h2 {
      display: flex;
      align-items: center;
    }
    .post-info {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .tips {
        display: flex;
        padding: 0;
        li {
          list-style: disc;
          margin-left: 25px;
          font-size: 14px;
          color: #666;
        }
      }
    }
  }
  .post-content {
    .markdown-text {
      img {
        width: 100%;
      }
    }
  }
}
</style>