<template>
  <div class="post-comment-item">
    <router-link :to=" `/user/${author.loginname}`">
      <el-avatar size="medium" :src="author.avatar_url"></el-avatar>
    </router-link>
    <div class="comment-right">
      <div class="comment-content">
        <span>{{author.loginname}}</span>
        <span class="index">{{index+1}}楼</span>
        <div v-html="content"></div>
        <span class="create_at">{{create_at|moment('from','now')}}</span>
      </div>
      <div class="like">
        <span @click="upsClick">

        <i v-if="is_uped" class="iconfont icon-dianzan1"></i>
        <i v-else class="iconfont icon-dianzan"></i>
        </span>
        <span>{{ups.length ? ups.length :''}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  props: ["author", "index", "create_at", "content", "id", "ups", "is_uped"],
  methods: {
    ...mapActions(['upsHandle']),
    upsClick() {
      this.upsHandle(this.id)
    }
  },
};
</script>

<style lang="less">
.post-comment-item {
  margin-bottom: 20px;
  display: flex;
  .comment-right {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
      border-radius: 20px;
      background-color: #f3f5f7;
      padding: 15px;
      margin-left: 10px;
      flex-grow: 1;
    .comment-content {
      img {
        width: 100%;
      }
      .index{
        font-size: 14px;
        margin-left: 15px;
        color: #08c;
      }
      .create_at{
        font-size: 12px ;
        color: #444;
      }
    }
  }
}
</style>